<template>
	<v-app id="app">
		<component is="script" src="https://code.jquery.com/jquery-1.12.3.min.js" async></component>
		<component is="script" src="https://cdnjs.cloudflare.com/ajax/libs/jspdf/0.9.0rc1/jspdf.min.js" async></component>
		<component is="script" src="https://cdn.jsdelivr.net/npm/chart.js@2.9.4" async></component>
		<div id="eto" class="myTable">
			<vue-headful title="Eto - Aranda Distribucion"></vue-headful>
			<b-form-group label="*Lote"
						  label-for="id_cultivo"
						  @submit.stop.prevent="handleSubmit">
				<v-autocomplete id="id_cultivo"
								v-model="cultivousado"
								@change="cambiofecha()"
								:items="opcultivos"
								placeholder="Elija el cultivo"
								required
								solo
								filled>
				</v-autocomplete>
			</b-form-group>
			<b-form-group label="Fecha Inicial" label-for="fechainiusada" style="display:inline-block;width:50%;">
				<b-form-input id="fechainiusada"
							  v-model="fechainiusada"
							  @change="cambiofecha()"
							  type="date"
							  placeholder="Ingrese la fecha en la que quiere el reporte"
							  invalid-feedback="Complete este campo"
							  required>
				</b-form-input>
			</b-form-group>
			<b-form-group label="Fecha Final" label-for="fechafinusada" style="display:inline-block;width:50%;">
				<b-form-input id="fechafinusada"
							  v-model="fechafinusada"
							  type="date"
							  @change="cambiofecha()"
							  placeholder="Ingrese la fecha en la que quiere el reporte"
							  invalid-feedback="Complete este campo"
							  required>
				</b-form-input>
			</b-form-group>
			<h2>Informe de BH</h2>
			<b-button @click="testFetch"
					  class="mb-4"
					  title="Recargar"
					  variant="light">
				<v-icon dark style="color: black">mdi-cached</v-icon>
				Actualizar
			</b-button>
			<b-button @click="generarPDF()"
					  id="btn_down_pdf"
					  class="mb-4 ml-2"
					  title="Generar PDF"
					  style="background-color: #00919F;color:white;">
				<svg xmlns="http://www.w3.org/2000/svg"
					 width="16"
					 height="16"
					 fill="currentColor"
					 class="bi bi-file-pdf-fill"
					 viewBox="0 0 16 16">
					<path d="M5.523 10.424c.14-.082.293-.162.459-.238a7.878 7.878 0 0 1-.45.606c-.28.337-.498.516-.635.572a.266.266 0 0 1-.035.012.282.282 0 0 1-.026-.044c-.056-.11-.054-.216.04-.36.106-.165.319-.354.647-.548zm2.455-1.647c-.119.025-.237.05-.356.078a21.035 21.035 0 0 0 .5-1.05 11.96 11.96 0 0 0 .51.858c-.217.032-.436.07-.654.114zm2.525.939a3.888 3.888 0 0 1-.435-.41c.228.005.434.022.612.054.317.057.466.147.518.209a.095.095 0 0 1 .026.064.436.436 0 0 1-.06.2.307.307 0 0 1-.094.124.107.107 0 0 1-.069.015c-.09-.003-.258-.066-.498-.256zM8.278 4.97c-.04.244-.108.524-.2.829a4.86 4.86 0 0 1-.089-.346c-.076-.353-.087-.63-.046-.822.038-.177.11-.248.196-.283a.517.517 0 0 1 .145-.04c.013.03.028.092.032.198.005.122-.007.277-.038.465z" />
					<path fill-rule="evenodd"
						  d="M4 0h8a2 2 0 0 1 2 2v12a2 2 0 0 1-2 2H4a2 2 0 0 1-2-2V2a2 2 0 0 1 2-2zm.165 11.668c.09.18.23.343.438.419.207.075.412.04.58-.03.318-.13.635-.436.926-.786.333-.401.683-.927 1.021-1.51a11.64 11.64 0 0 1 1.997-.406c.3.383.61.713.91.95.28.22.603.403.934.417a.856.856 0 0 0 .51-.138c.155-.101.27-.247.354-.416.09-.181.145-.37.138-.563a.844.844 0 0 0-.2-.518c-.226-.27-.596-.4-.96-.465a5.76 5.76 0 0 0-1.335-.05 10.954 10.954 0 0 1-.98-1.686c.25-.66.437-1.284.52-1.794.036-.218.055-.426.048-.614a1.238 1.238 0 0 0-.127-.538.7.7 0 0 0-.477-.365c-.202-.043-.41 0-.601.077-.377.15-.576.47-.651.823-.073.34-.04.736.046 1.136.088.406.238.848.43 1.295a19.707 19.707 0 0 1-1.062 2.227 7.662 7.662 0 0 0-1.482.645c-.37.22-.699.48-.897.787-.21.326-.275.714-.08 1.103z" />
				</svg>
				Generar PDF
			</b-button>
			<div id="graficos">
				<div style="max-width:100%;display:inline-block;text-align:center;"><canvas id="chart" width="1500" height="500" style="text-align:center;"></canvas></div>
			</div>
			<!-- ================ELIMINAR VARIOS GASTOS======================== -->
			<div>
				<b-modal ref="my-modal"
						 id="modal-eliminarTodo"
						 hide-footer
						 title="Eliminar"
						 ok-only>
					<div class="d-block text-center" v-if="selected.length === rows">
						<h3>¿Esta seguro de eliminar todos los registros ?</h3>
					</div>
					<div class="d-block text-center" v-else>
						<h3>¿Esta seguro de eliminar {{ selected.length }} registros ?</h3>
					</div>

					<b-button class="mt-2" block @click="hideModal" title="Volver Atras">
						Volver Atras
					</b-button>

					<b-button class="mt-3"
							  variant="danger"
							  block
							  title="Eliminar"
							  @click="delete_all_Eto()">
						Eliminar
					</b-button>
				</b-modal>
			</div>
			<!-- ======================================== -->
			<!-- ======== Formulario de Busqueda ======== -->
			<b-form-group label-for="filter-input"
						  label-align-sm="right"
						  label-size="sm"
						  class="mb-0"
						  style="width: 100%; padding-bottom: 1em"
						  v-show="rows > 0">
				<b-input-group size="sm">
					<b-form-input id="filter-input"
								  v-model="filter"
								  type="search"
								  placeholder="Buscar registros"></b-form-input>

					<b-input-group-append>
						<b-button :disabled="!filter" @click="filter = ''">Limpiar</b-button>
					</b-input-group-append>
				</b-input-group>
			</b-form-group>
			<!-- ======================================== -->
			<div>
				<pre>Cantidad de registros: {{ rows }}</pre>
			</div>
			<div id="pdftabla">
				<div v-for="(item, index) in periodocompleto" :key="index">
					<h4>Mes: {{item.mesusado}}</h4>
					<b-table :fields="llenarfields(item.tabla_fechas)"
							 striped
							 sortable
							 responsive
							 hover
							 :items="item.tabla_eto"
							 show-empty
							 :per-page="perPage"
							 :current-page="currentPage"
							 :sticky-header="false"
							 :no-border-collapse="true"
							 ref="tablaregistros"
							 id="tablaregistros"
							 :filter="filter"
							 @filtered="onFiltered">
						<template #empty="">
							<b>No hay registros para mostrar</b>
						</template>

						<template slot="cell(valor)" slot-scope="data">
							<b>{{ data.value}}</b>
						</template>

						<template #cell(selected)="{ rowSelected }">
							<template v-if="rowSelected">
								<span aria-hidden="true">&check;</span>
								<span class="sr-only">Selected</span>
							</template>
							<template v-else>
								<span aria-hidden="true">&nbsp;</span>
								<span class="sr-only">Not selected</span>
							</template>
						</template>
						<template #row-details="row">
							<b-card title="Datos del eto: ">
								<div>
									<b-list-group horizontal>
										<b-list-group class="col-3">
											<b-list-group-item><b>Id eto:</b> {{ row.item.id_eto }}</b-list-group-item>
											<b-list-group-item>
												<b>Numero de ticket:</b>
												{{ row.item.nro_ticket }}
											</b-list-group-item>
											<b-list-group-item>
												<b>Descripcion:</b>
												{{ row.item.descripcion }}
											</b-list-group-item>
											<b-list-group-item><b>Total:</b> {{ row.item.total }}</b-list-group-item>
											<b-list-group-item><b>Fecha:</b> {{ row.item.fecha }}</b-list-group-item>
										</b-list-group>
										&nbsp;
									</b-list-group>
								</div>
							</b-card>
						</template>
					</b-table>
				</div>
			</div>
			
			<!-- ======== Tabla con los registros ======= -->
			<!-- ================ELIMINAR GASTO SALIENTE======================== -->

			<b-modal id="modal_eliminar"
					 ref="my-modal"
					 hide-footer
					 title="Eliminar"
					 ok-only>
				<div class="d-block text-center">
					<h3>
						¿Esta seguro de eliminar los datos de
						{{ infoEliminar.eto.id_eto }} - Nro ticket
						{{ infoEliminar.eto.patente }}?
					</h3>
				</div>
				<b-button class="mt-2" block @click="hideModal" title="Volver Atras">Volver Atras</b-button>
				<b-button class="mt-3"
						  variant="danger"
						  block
						  @click="deleteEto(infoEliminar.eto.id_eto)"
						  title="Eliminar">
					Eliminar
				</b-button>
			</b-modal>
			<b-container fluid>
				<b-col class="my-1">
					<b-pagination v-model="currentPage"
								  align="center"
								  pills
								  :total-rows="rows"
								  :per-page="perPage"
								  aria-controls="table_eto">
					</b-pagination>
				</b-col>
			</b-container>
		</div>
	</v-app>
</template>

<script>
    let api = new URL("http://45.162.168.170:8081");
	api.pathname = "eto";
	//api.port = 8081;
    import { APIControler } from "@/store/APIControler";

	import axios from "axios";
    import html2pdf from 'html2pdf.js'
    import Chart from 'chart.js/auto';
    import { getRelativePosition } from 'chart.js/helpers';

	export default {
		data() {
			return {
				tabla_eto: [],
				campos: [],
				fields: [
                    { key: "mes", label: "Mes", sortable: true },
				],
				selected: [],
                opcultivos: [
                    { value: null, text: "Elija un cultivo", disabled: true },
				],
                cultivousado: null,
				periodocompleto: [],
				fechas: [],
				bhs: [],
				listacli: [],
				pps: [],
				etos: [],
                datachart: {
                    labels: [],
                    datasets: []
                },
				filter: null,
				totalRows: 1, //Total de filas
				currentPage: 1, //Pagina actual
				perPage: 10, // Datos en la tabla por pagina
				buscar: "",
				editar: {},
				lotesusados: [],
				fechainiusada: null,
				etous: true,
				campousado: null,
				fechafinusada: null,
				cultivousado: null,
				infoEliminar: {
					id: "modal_eliminar",
					eto: -1,
				},
				btn_down_pdf: true, //Desabilito los botones, hasta que muestre los datos
				btn_del_full: true,
				msj_tabla: " Presione 'Mostrar' para ver los regitros ",
				btn_mostrar: false,
				btn_editar: false,
				btn_eliminar: false,
				btn_select: false,
				btn_limpiar: true,
			};
		},
		computed: {
			rows() {
                return (this.totalRows = this.tabla_eto.length);
			},
			rowsFilter() {
				return this.totalRows;
			},
			id() {
                return this.tabla_eto.id_eto;
			},
			sortOptions() {
				// Create an options list from our fields
				return this.fields
					.filter((f) => f.sortable)
					.map((f) => {
						return { text: f.label, value: f.key };
					});
			},
		},
		methods: {
			cambiofecha()
			{
                this.periodocompletardo();
			},
			GuardarPrecioRef(data)
			{
				let fechaza = data.field.key
                fechaza = fechaza.split('-')[2] + '-' + fechaza.split('-')[1] + '-' + fechaza.split('-')[0]
				if (this.findetofecha(fechaza) == null) {
					let eto = {
						fecha: fechaza,
						eto: data.value,
						id_cultivo: this.cultivousado,
					};
					this.postEto(eto);
				} else
				{
                    let eto = this.findetofecha2(fechaza)
					eto.eto = data.value;
                    this.putEto(eto);
				}
				console.log(data)
				this.etous = true;
			},
            async getCultivos() {
                let productosAPI = new APIControler();
                productosAPI.apiUrl.pathname = "cultivos/";
                this.listacli = await productosAPI.getData();
                let option
                this.listacli.forEach((element) => {
                    option = {};
                    option.value =
                        "http://45.162.168.170:8081/cultivos/" + element.id_cultivo + "/";
                    option.text = element.id_campo.split('/')[4] + ' - ' + element.tipo + ' - ' + element.tiposuelo;
                    this.opcultivos.push(option);
                });
			},
            async generarPDF() {
                const $elementoParaConvertir = document.getElementById('graficos'); // <-- Aquí puedes elegir cualquier elemento del DOM
                html2pdf()
                    .set({
                        margin: 1,
                        filename: 'GraficosPeriodoDe' + this.fechainiusada + ' a ' + this.fechafinusada + '.pdf',
                        image: {
                            type: 'jpeg',
                            quality: 0.98
                        },
                        html2canvas: {
                            scale: 3, // A mayor escala, mejores gráficos, pero más peso
                            letterRendering: true,
                        },
                        jsPDF: {
                            unit: "in",
                            format: "a3",
                            orientation: 'landscape' // landscape o portrait
                        }
                    })
                    .from($elementoParaConvertir)
                    .save()
                    .catch(err => console.log(err));
                const $elementoParaConvertir2 = document.getElementById('pdftabla'); // <-- Aquí puedes elegir cualquier elemento del DOM
                html2pdf()
                    .set({
                        margin: 1,
                        filename: 'TablaPeriodoDe' + this.fechainiusada + ' a ' + this.fechafinusada + '.pdf',
                        image: {
                            type: 'jpeg',
                            quality: 0.98
                        },
                        html2canvas: {
                            scale: 3, // A mayor escala, mejores gráficos, pero más peso
                            letterRendering: true,
                        },
                        jsPDF: {
                            unit: "in",
                            format: "a3",
                            orientation: 'landscape' // landscape o portrait
                        }
                    })
                    .from($elementoParaConvertir2)
                    .save()
                    .catch(err => console.log(err));
            },
            async postEto(eto) {
                let gastoSalienteAPI = new APIControler();
                gastoSalienteAPI.apiUrl.pathname = "eto/";
                this.respuesta = await gastoSalienteAPI.postData(eto);
			},
            async postBh(bh) {
                let gastoSalienteAPI = new APIControler();
                gastoSalienteAPI.apiUrl.pathname = "bh/";
                this.respuesta = await gastoSalienteAPI.postData2(bh);
            },
            armarGrafico() {
                this.datachart.labels = this.fechas;
                let datasetear1 = {
                    label: 'DR',
                    data: [],
                    borderColor: 'rgb(255, 165, 0)',
                    backgroundColor: 'rgb(255, 165, 0)',
                    hoverOffset: 4,
                    yAxisID: 'y',
                }; 
                this.fechas.forEach((fecha) => {
                    let dia = fecha.split('-')[0];
                    let mes = fecha.split('-')[1];
					let anio = fecha.split('-')[2];
					let fechaorde = anio + '-' + mes + '-' + dia
					console.log('sacao')
                    console.log(this.getCultivo(this.cultivousado.split('/')[4]).calculodr)
					datasetear1.data.push(this.getCultivo(this.cultivousado.split('/')[4]).calculodr);
                });
                this.datachart.datasets.push(datasetear1);
                let ctx2 = document.getElementById('chart');
                if (this.chart) {
                    this.chart.destroy();
                }
                this.chart = new Chart(ctx2, {
                    type: 'line',
                    data: this.datachart,
                    options: {
                        title: {
                            text: "Calculo de BH",
                            display: true
                        }
                    },
                    interaction: {
                        mode: 'index',
                        intersect: false,
                    },
                });
			},
			async putBh(fechuca, bh) {
				let bhusado = this.findbhfecha2(fechuca)
				bhusado.bh = bh;
                let respuesta = "vacio";
                await axios
                    .put(
                        "http://45.162.168.170:8081/bh/" +
                        bhusado.id_bh +
                        "/",
                        bhusado
                    )
                    .then(function (data) {
                        swal("Operación Exitosa", " ", "success");
                    })
                    .catch(function (error) {
                        const mje =
                            error.response.status < 500
                                ? "Los datos no son válidos"
                                : "Se ha detectado un problema ";
                        swal("¡ERROR!", mje, "error");
                        respuesta = error.response.data;
                    });
            },
            async putEto(eto) {
                let respuesta = "vacio";
                await axios
                    .put(
                        "http://45.162.168.170:8081/eto/" +
                        eto.id_eto +
                        "/",
                        eto
                    )
                    .then(function (data) {
                        swal("Operación Exitosa", " ", "success");
                    })
                    .catch(function (error) {
                        const mje =
                            error.response.status < 500
                                ? "Los datos no son válidos"
                                : "Se ha detectado un problema ";
                        swal("¡ERROR!", mje, "error");
                        respuesta = error.response.data;
                    });
			},
			getLotes(campo)
			{
				let lotes = [];
				this.listacli.forEach((cult) =>
				{
					if (parseInt(cult.id_campo.split('/')[4]) == campo.id_campo)
					{
						lotes.push(cult);
					}
				})
				return lotes;
			},
			async testFetch() {
				try {
					const res = await fetch(api);
					const data = await res.json();
                    this.datachart.datasets = [];
					var lista_eto = data.results;
                    let datasetear3 = {
                        label: 'BH',
                        data: [],
                        borderColor: 'rgb(43, 165, 0)',
                        backgroundColor: 'rgb(43, 165, 0)',
                        hoverOffset: 4,
                        yAxisID: 'y',
                    };
					this.periodocompleto.forEach((opcion) => {
						opcion.tabla_eto = [];
                        let fila0 = {};
                        fila0['valor'] = 'Ppef'
						let fila = {};
						fila['valor'] = 'Eto'
                        let fila2 = {};
						fila2['valor'] = 'Riego'
                        let fila3 = {};
						fila3['valor'] = 'Kc'
                        let fila4 = {};
						fila4['valor'] = 'ETP'
                        let fila5 = {};
						fila5['valor'] = 'BH'
						let bhanterior = -999;
						opcion.tabla_fechas.forEach((fecha) => {
							let fechuca = fecha.split('-')[2] + '-' + fecha.split('-')[1] + '-' + fecha.split('-')[0]
							let etofecha = this.findetofecha(fechuca)
							fila[fecha] = etofecha;
							let riegofecha = this.findriegofecha(fechuca)
							fila2[fecha] = riegofecha;
							let kcmes = this.findkcmes(opcion.mesusado)
							fila3[fecha] = kcmes;
							let pp = this.findppfecha(fechuca)
                            let ppef = Math.round((pp * ((125 - (0.2 * pp)) / 125))*100)/100
							fila0[fecha] = ppef;
							let etp = Math.round(etofecha * kcmes*100)/100;
							fila4[fecha] = etp;
							let fechabh = (parseInt(fecha.split('-')[0]) - 1) + '-' + fecha.split('-')[1] + '-' + fecha.split('-')[2];
                            let fechucabh = fechabh.split('-')[2] + '-' + fechabh.split('-')[1] + '-' + fechabh.split('-')[0]
							if (bhanterior == -999) {
                                bhanterior = this.findbhfecha(fechucabh);
							} else
							{
                                bhanterior = fila5[fechabh];
							}
							
							let bh = Math.round((parseFloat(bhanterior) + parseFloat(ppef) - parseFloat(etp) + parseFloat(riegofecha)) * 10) / 10;
							if (!isNaN(bh))
							{
                                if (this.findbhfecha(fechuca) == null) {
                                    let bhpost =
                                    {
                                        fecha: fechuca,
                                        bh: bh,
                                    }
                                    this.postBh(bhpost);
                                } else {
                                    this.putBh(fechuca, bh);
                                }
							}
							fila5[fecha] = bh;
							datasetear3.data.push(bh);
						})
						console.log(fila);
                        opcion.tabla_eto.push(fila0);
						opcion.tabla_eto.push(fila);
						opcion.tabla_eto.push(fila2);
						opcion.tabla_eto.push(fila3);
						opcion.tabla_eto.push(fila4);
                        opcion.tabla_eto.push(fila5);
					})
                    this.datachart.datasets.push(datasetear3);
					this.armarGrafico();
				} catch (error) {
					console.log(error);
				}
			},
            async getKcs() {
                let gastoSalienteAPI = new APIControler();
                gastoSalienteAPI.apiUrl.pathname = "kc/";
                this.kcs = await gastoSalienteAPI.getData();
			},
            getCampo(id) {
                if (this.campos == []) {
                    return getCampos().then(() => {
                        return this.getCampo(id)
                    })
                }
                else {
                    return this.campos.find(s => {
                        return s.id_campo == id
                    })
                }
			},
            getCultivo(id) {
                if (this.listacli == []) {
                    return getCultivos().then(() => {
                        return this.getCultivo(id)
                    })
                }
                else {
                    return this.listacli.find(s => {
                        return s.id_cultivo == id
                    })
                }
            },
            async getBhs() {
                let gastoSalienteAPI = new APIControler();
                gastoSalienteAPI.apiUrl.pathname = "bh/";
                this.bhs = await gastoSalienteAPI.getData();
            },
            findriegofecha(fecharda) {
                console.log(this.riegos);
                let riegofecha = this.riegos.find((s) => {
                    return (s.fecha == fecharda && s.id_cultivo == this.cultivousado);
                })
                if (riegofecha == undefined) {
                    return null
                } else {
                    return riegofecha.riego;
                }
