				{
					let opcion = {
						mesusado: mes,
						tabla_fechas: [],
						tabla_eto: [],
					};
					this.fechas.forEach((fecha) =>
					{
                        let fechaformat = fecha.split('-')[2] + '-' + fecha.split('-')[1] + '-' + fecha.split('-')[0]
						let fechuca = new Date(fechaformat);
						let mesuco = (fechuca.getMonth() + 1)
						console.log(mesuco)
						let mesito;
                        switch (mesuco) {
                            case 1:
                                mesito = 'Enero'
