            mesesperiodo() {
                this.meses = [];
                let fechaini = new Date(this.fechainiusada)
                let fechafin = new Date(this.fechafinusada)
                while (fechaini.getTime() <= fechafin.getTime()) {
					let mes;
                    let mesuco = fechaini.getMonth() + 1
					switch (mesuco) {
						case 1:
							mes = 'Enero'
							break;
						case 2:
							mes = 'Febrero'
							break;
						case 3:
